import { get } from '../utils/request'

// 
export const getSinger = (area = -1, type = -1) => get('/artist/list', { type: type, area: area }).then(res => res.data)
// 
export const getSingerDetail = (id) => get('/artist/detail', { id: id }).then(res => res.data)
// 50
export const getTop50 = (id) => get('/artist/top/song', { id: id }).then(res => res.data)
// mv
export const getMV = (id) => get('/artist/mv', { id: id }).then(res => res.data)
// 
export const getAlbum = (id) => get('/artist/album', { id: id }).then(res => res.data)
// 
export const getDesc = (id) => get('/artist/desc', { id: id }).then(res => res.data)
// 
export const getSimi = (id) => get('/simi/artist', { id: id }).then(res => res.data)
// 
export const getAlbumDetail = (id) => get('/album', { id: id }).then(res => res.data)
