import { get } from '../utils/request'
// api

// 
// 1.key
export const getKey = () => get('/login/qr/key', { timestape: Date.now() }).then(res => res.data)
// 2.
export const getQR = (key, qrimg = true) => get('/login/qr/create', { key: key, qrimg: qrimg, timestape: Date.now() }).then(res => res.data)
// 3.
export const checkQR = (key) => get('/login/qr/check', { key: key, timestape: Date.now() }).then(res => res.data)

// 
export const getCellPhone = (phone, password) => get('/login/cellphone', { timestape: Date.now(), phone: phone, password: password }).then(res => res.data)

// 
export const getAccount = () => get('/user/account').then(res => res.data)

// logout
export const getLogout = () => get('/logout')
