import { get } from '../utils/request.js'

// 
export const getDaliySongs = () => get('/recommend/songs', { timestape: Date.now() }).then(res => res.data)
// 
export const getRecommendList = () => get('recommend/resource', { timestape: Date.now() }).then(res => { console.log(res.status, 'playlist.statue'); return res.data })
// 
export const getPlayListDetail = (id) => get('playlist/detail', { timestape: Date.now(), id: id }).then(res => res.data)
// 10
export const getTrackAll = (id) => get('/playlist/track/all', { timestape: Date.now(), id: id }).then(res => res.data)
// 
export const getPersonalized = () => get('/personalized', { timestape: Date.now(), limit: 10 }).then(res => res.data)
// 
export const getUserDetail = (id) => get('/user/detail', { timestape: Date.now(), id: id }).then(res => res.data)
// URL
export const getSongUrl = (id, level) => get('/song/url/v1', { timestape: Date.now(), id: id, level: level }).then(res => res.data)
// 
export const getComment = (id) => get('/comment/playlist', { limit: 100, timestape: Date.now(), id: id }).then(res => res.data)
// 
export const getSubscriber = (id) => get('/playlist/subscribers', { id: id, timestape: Date.now() }).then(res => res.data)
// 
export const getCatList = () => get('/playlist/catlist', { timestape: Date.now() }).then(res => res.data)
// 
export const getHotCatList = () => get('/playlist/hot', {}).then(res => res.data)
// 
export const getTopPlayList = (cat) => get('/top/playlist', { timestape: Date.now(), cat: cat }).then(res => res.data)
// 
export const getHighQualityTag = () => get('/playlist/highquality/tags', { timestape: Date.now() }).then(res => res.data)
// 
export const getHighQuality = (cat = '', limit = 50) => get('/top/playlist/highquality', { cat: cat, limit: limit }).then(res => res.data)
